import { Dispatch } from "react"

export interface UIStore {
  UIState: UIState
  UIDispatch: Dispatch<UIActions>
}

export interface UIState {
  isDrawerOpen: boolean
}

export const initialUIState = { isDrawerOpen: false }

export const UIReducer = (state: UIState, action: UIActions) => {
  switch (action.type) {
    case "UI_OPEN_DRAWER":
      return { isDrawerOpen: true }
    case "UI_CLOSE_DRAWER":
      return { isDrawerOpen: false }
    default:
      return state || initialUIState
  }
}

//Actions
interface UIOpenDrawer {
  type: "UI_OPEN_DRAWER"
}
interface UICloseDrawer {
  type: "UI_CLOSE_DRAWER"
}

type UIActions = UIOpenDrawer | UICloseDrawer
