import React, { createContext, useContext, useReducer } from "react"
import { initialUIState, UIReducer, UIStore } from "./Reducers/ui.reducer"

type MainStore = UIStore

const StateContext = createContext({} as MainStore)

export const StateProvider = ({ children }: any) => {
  const [UIState, UIDispatch] = useReducer(UIReducer, initialUIState)

  const MainStore: MainStore = {
    UIState,
    UIDispatch,
  }

  return (
    <StateContext.Provider value={MainStore}>{children}</StateContext.Provider>
  )
}

export const useAppStore = () => useContext(StateContext)
