import lightenDarkenColor from "../utils/lightendarkencolor"

interface IVariant {
  light?: string
  main: string
  dark?: string
  contrastText: string
}

export interface Theme {
  primary: IVariant
  secondary: IVariant
  error: IVariant
  skin: IVariant
  dp: {
    dp1: string
    dp4: string
  }
  getLinearGradient: () => string
  drawer: {
    width: string
    getRest: () => string
  }
}

export interface Themed {
  theme: Theme
}

const primary = "#E7278C"
const secondary = "#F0CF65"
const error = "#C96F86"

export const theme: Theme = {
  primary: {
    light: lightenDarkenColor(primary, 20),
    main: primary,
    dark: lightenDarkenColor(primary, -20),
    contrastText: "black",
  },
  secondary: {
    light: lightenDarkenColor(secondary, 20),
    main: secondary,
    dark: lightenDarkenColor(secondary, -20),
    contrastText: "black",
  },
  error: {
    light: lightenDarkenColor(error, 20),
    main: error,
    dark: lightenDarkenColor(error, -20),
    contrastText: "white",
  },
  skin: {
    main: "white",
    dark: "#212121",
    contrastText: "#787878",
  },
  dp: {
    dp1: `0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12)`,
    dp4: `0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14),
    0px 1px 10px 0px rgba(0, 0, 0, 0.12)`,
  },
  getLinearGradient: function() {
    return `linear-gradient(${this.primary.main}, ${this.primary.main})`
  },
  drawer: {
    width: "250px",
    getRest: function() {
      return `calc(100% - ${this.width})`
    },
  },
}
